import Router from 'vue-router'
import HomePage from '@/components/HomePage.vue'

const router = new Router({
    mode: 'history',
    routes: [
        {
            name: 'home',
            path: '/',
            component: HomePage
        },
        {
            name: 'login',
            path: '/login',
            // component: LoginPage
        },
        {
            name: 'register',
            path: '/register',
            // component: RegisterPage
        },
        {
            name: 'blog-details',
            path: '/blogs/:id',
            props: true,
            // component: BlogDetailPage,
        },
        {
            name: 'blogs-tags',
            path: '/tags',
            props: true,
            // component: TagsPage
            children: [
                {
                    name: 'blogs-tag',
                    path: ':tag',
                    props: true,
                    // component: BlogsListPage
                }
            ]
        },
        {
            name: 'blogs-category',
            path: '/categories/:category',
            props: true,
            // component: BlogsListPage
        },
        {
            name: 'newsletter',
            path: '/newsletter',
            // component: NewsletterPage
        },
        {
            name: 'add-blog',
            path: '/add',
            // component: AddBlogPage
        },
        {
            name: 'not-found',
            path: '*',
            // component: PageNotFound
        },
    ]
})

export default router