<template>
    <el-aside width="20%">
        <el-row class="tac">
            <el-col>
                <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
                    background-color="#8D9EFF" text-color="#001253" active-text-color="#0027b3" :collapse="isCollapse">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>Categories</span>
                        </template>
                            <el-menu-item index="1-1">C</el-menu-item>
                            <el-menu-item index="1-2">C++</el-menu-item>
                            <el-menu-item index="1-3">Python</el-menu-item>
                            <el-menu-item index="1-4">Java</el-menu-item>
                            <el-menu-item index="1-5">JavaScript</el-menu-item>
                            <el-menu-item index="1-6">C#</el-menu-item>
                            <el-menu-item index="1-7">Ruby</el-menu-item>
                    </el-submenu>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span>Tags</span>
                    </el-menu-item>
                    <el-menu-item index="3" disabled>
                        <i class="el-icon-document"></i>
                        <span>Navigator Three</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span>Navigator Four</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
        </el-row>
    </el-aside>
</template>

<script>
export default {
    name: 'SideBar',
    data() {
        return {
            windowWidth: window.innerWidth,
            isCollapse:false
        }
    },
    mounted() {
        window.onresize = () => {
            this.windowWidth = window.innerWidth
            this.isCollapse = this.windowWidth < 710
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        }
    },
    created() {
        this.isCollapse = window.innerWidth < 710
    }
}
</script>

<style scoped>
::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}
el-aside{
    position: fixed;
}
i{
    color: #001253;
}
</style>